.splash {
  -ms-flex-align: start;
      align-items: flex-start;
  background-image: url("/assets/images/ThisChristography-20190323-001.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  height: 100vh;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 32px;
  width: 100%;
}

.home img {
  max-width: 100%;
}

.home a.image {
  border: none;
  width: 100%;
}

.home a.image img {
  border-radius: 5px;
}

.home a.image:hover img {
  -webkit-box-shadow: 1px 2px 6px rgba(48, 95, 123, 0.5);
          box-shadow: 1px 2px 6px rgba(48, 95, 123, 0.5);
}

@media (min-width: 768px) {
  .splash {
    background-image: url("/assets/images/ThisChristography-20190323-002.jpg");
  }
}


